import React from 'react';
import "./Home.css"
import Particles from 'react-particles-js';


const particlesOptions ={
  particles: {
    number:{
      value:25,
      density:{
        enable:true,
        value_area:500
      }
    },
    line_linked: {
      shadow: {
        enable: true,
        color: "#3CA9D1",
        blur: 5
      }
    }
  }
}

function Home(){
	return(
		<div className="pt6">
			<Particles className='particles' 
              params={particlesOptions}
          	/>
			<h1>Home to be coded :)</h1>
		</div>
		)
	
}
export default Home