import React from 'react';
import './picture.css';
import photo from './Photo.jpg';
function Picture(){
	return(
		<div className='dib br3 pt6 pl6 fl h5'>
			<div style={{justifyContent:"center",width:"auto",maxWidth:"30%",justifyContent:"center"}} className=' zoom'>{/*added justifyContent*/}
				<img alt="Personal_Pic" src={photo} />
			
			</div>
		</div>
		)
}
export default Picture;