import React from 'react';
import './self.css';
import tech from './Technical_Expertise.png';
import personality from './SPI27_Factor.png';

function Self(){
	return(
		<div>
			<div style={{height:"auto",minHeight:"500px",width:"auto"}} className=" fl dib br3 pa3 sans-serif">
				<div style={{margin:"0px 33%"}} className="tc br3 bg-light-blue">
					<h2>Self-Analysis</h2>
				</div>
			<div style={{justifyContent:"center"}} className="row">
				<div style={{width:"auto",maxWidth:"40%"}} className="pa1 pt4 zoom bg-pink br3">
					<img alt="Technical introspection Pic" src={tech}/>
				</div>
				<div style={{width:"auto",maxWidth:"40%"}} className="pa1 zoom bg-pink br3">
					<img alt="Personality Test Pic" src={personality}/>
				</div>
			</div>
			</div>
		</div>
		)
}
export default Self