import React from 'react';

function Navigation({onRouteChange}){
	return(
		<header className="bg-black-90 fixed w-100 ph3 pv3 pv4-ns ph4-m ph5-l">
			<nav className="f6 fw6 ttu tracked">
				<button onClick = {()=>onRouteChange('Home')} className='f3 link dim black pointer'>
				Home
				</button>
				<button onClick = {()=>onRouteChange('About')} className='f3 link dim black  pointer'>
				About
				</button>
				<button onClick = {()=>onRouteChange('Digital')} className='f3 link dim black  pointer'>
				Digital Footprint
				</button>
				<button onClick = {()=>onRouteChange('Contact')} className='f3 link dim black  pointer'>
				Contact
				</button>
			</nav>
		</header>
		)
}
export default Navigation