import React,{Component} from 'react';
import Picture from './Components/Portfolio/Picture';
import Gen_info from './Components/Portfolio/Gen_info';
import Contact from './Components/Portfolio/contact';
import Education from './Components/Portfolio/education';
import Skills from './Components/Portfolio/Skills';
import Workex from './Components/Portfolio/Workex';
import Certificates from './Components/Portfolio/Certificates';
import Self from './Components/Portfolio/Self';
import Navigation from './Components/Navigation';
import Home from './Components/Home';
import Blog from './Components/Blog';
import Contact_main from './Components/Contact_main';

import './App.css';

class App extends Component {
  constructor(){
    super();
    this.state={
      route:'About',
    }
  }
  RouteChange=(route)=>{
    this.setState({route:route});
  }
  render(){
  return (
    <div>
      <Navigation onRouteChange={this.RouteChange}/>
      {this.state.route==='About'?
      (<div>
        <div className="body bg-lightest-blue" ></div>
        <div className="TopGrad"></div>
        <div className="SideGrad"></div>
        <div style={{justifyContent:"center"}} className="center row">
          <div style={{width:"auto",maxWidth:"50%"}} className="column First-Column">
            <Picture/>
            <Contact/>
            <Skills/>
            <Certificates/>
          </div>
          <div style={{width:"auto",maxWidth:"50%"}} className="column second-Column">
            <Gen_info/>
            <Education/>
            <Workex/>
            <Self/>
          </div>
        </div>
      </div>)
      :(
        this.state.route==="Home"?
        <Home onRouteChange={this.RouteChange}/>
        :(this.state.route==="Digital"?
          <Blog onRouteChange={this.RouteChange}/>
          :<Contact_main onRouteChange={this.RouteChange}/>
          )
        )
    }
    </div>
  )
}
}

export default App;
